<template>
  <div class="row">
    <div class="col-lg-12">
      <div class="row">
        <div class="col-lg-4 offset-1">
          <div class="table-responsive">
            <table>
              <tr style="text-align: left">
                <th><h3>{{my_firm.full_name}}</h3></th>
              </tr>
              <tr style="text-align: left">
                <th>{{my_firm.address}}</th>
              </tr>
              <tr style="text-align: left">
                <th>{{my_firm.taxpayer_number}}</th>
              </tr>
              <tr style="text-align: left">
                <th>{{my_firm.bank_requisites}}</th>
              </tr>
            </table>
          </div>
        </div>
      </div>
      <div class="table-responsive">
        <table class="table">
          <tr>
            <th>№</th>
            <th>Наименование и краткая характеристика услуги</th>
            <th>Ед.изм.</th>
            <th>Кол-во</th>
            <th>Цена,руб.</th>
            <th>Сумма без НДС, руб.</th>
            <th>Ставка НДС, %</th>
            <th>Сумма НДС, руб.</th>
            <th>Сумма с НДС руб.</th>
          </tr>
          <tr v-for="service, index in invoice.services_obj">
            <td>{{index+1}}</td>
            <td>{{service.name}}</td>
            <td>{{service.unit}}</td>
            <td>{{service.count}}</td>
            <td>{{service.price}}</td>
            <td>{{service.cost_wo_vat}}</td>
            <td>{{service.vat}}</td>
            <td>{{service.vat_value}}</td>
            <td>{{service.cost_with_vat}}</td>
          </tr>
          <tr>
            <td colspan="5">Итого</td>
            <td>{{invoice.price_wo_vat_value}}</td>
            <td></td>
            <td>{{invoice.vat_value}}</td>

            <td >{{invoice.price_with_vat_value}}</td>
          </tr>
        </table>
      </div>
        <form>
          <div class="form-group row">
            <label for="vat_verbose" class="col-form-label col-lg-2"> Сумма НДС прописью</label>
            <input id="vat_verbose" class="form-control col-lg-6" type="text" readonly v-model="invoice.vat_verbose">
          </div>
          <div class="form-group row">
            <label for="price_with_vat_verbose" class="col-form-label col-lg-2"> ВСЕГО с НДС прописью: </label>
            <input id="price_with_vat_verbose" class="form-control col-lg-6" type="text" readonly v-model="invoice.price_with_vat_verbose">
          </div>
          <div class="form-group row">
            <textarea class="form-control col-lg-6" type="text">
              Вышеперечисленные услуги выполнены полностью и в срок. Заказчик претензий по объему,качеству и срокам оказанных услуг не имеет</textarea>
          </div>
        </form>
      <div class="row ">
        <div class="col-lg-10 offset-1 ">
          <div class="table-responsive">
            <table style="width: 100%;">
              <tr>
                <td style="width: 50%;">
                  <ul class="list-group" >
                    <li class="list-group-item" >
                      {{my_firm.full_name}}
                    </li>
                    <li class="list-group-item">
                      {{my_firm.address}}
                    </li>
                    <li class="list-group-item">
                      {{my_firm.taxpayer_number}}
                    </li>
                    <li class="list-group-item">
                      {{my_firm.bank_requisites}}
                    </li>
                  </ul>
                </td>

                <td style="width: 50%;">
                  <ul class="list-group" >
                    <li class="list-group-item" >
                      {{invoice.partner_obj.full_name}}
                    </li>
                    <li class="list-group-item">
                      {{invoice.partner_obj.address}}
                    </li>
                    <li class="list-group-item">
                      {{invoice.partner_obj.taxpayer_number}}
                    </li>
                    <li class="list-group-item">
                      {{invoice.partner_obj.bank_requisites}}
                    </li>
                  </ul>
                </td>
              </tr>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import constants from '@/common/constants'
    export default {
      name: "Act",
      data(){
        return{
          act:{},
          invoice:{},
          my_firm:{},
        }
      },
      methods:{
        get_invoice:function (invoice_id) {
          this.$http.get(constants.URL_PREFIX+'documents/api/documents/'+invoice_id+'/').then(response => {
            this.invoice=response.data})
        },
        get_my_firm:function () {
          this.$http.get(constants.URL_PREFIX+'partners/api/partner/?my_firm=True').then(response => this.my_firm = response.data[0])
        },
      },
      created(){
        this.get_invoice(this.$route.params.invoice)
        this.get_my_firm()
      }
    }
</script>

<style scoped>

</style>



