<template>
  <div class="row">

    <div class="col-lg-10 offset-1">
      <div class="form-group">
        <router-link :to="{name:'invoice', params:{partner:$route.params.partner}}" class="btn btn-dark form-control mt-2" >
          Добавить счет
      </router-link>
      </div>

      <ul class="list-group" v-for="invoice, index in invoices">
        <li class="list-group-item list-group-item-action">
          <router-link :to="{name:'invoice', params:{id:invoice.id}}" class="btn btn-default btn-dark list-group-item-action" style="color: lightskyblue">
            <div class="row">
              <div class="col-lg-2">
                {{index+1}}
              </div>
              <div class="col-lg-6">
                {{invoice.number}}
              </div>
              <div class="col-lg-4">
                {{invoice.date}}
              </div>
            </div>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import constants from '@/common/constants'
    export default {
      name: "Invoices",
      data(){
        return{
          invoices:[]
        }
      },
      methods: {
        get_invoices: function () {
          this.$http.get(constants.URL_PREFIX + 'documents/api/documents/?partner='+this.$route.params.partner).then(response => {
            this.invoices = response.data
          })
        }
      },
      created(){
        this.get_invoices()
      }
    }
</script>

<style scoped>

</style>



