<template>
  <div class="row">
      <div class="col-lg-12">
        <form class="">
          <!--<span v-if="(($route.path).indexOf('postal') == '1')"-->
                <!--@click="$store.dispatch('PartnerDetailStore/set_postal_address', mapState([pa]))"-->
          <!--&gt;Совпадает с юридическим</span>-->
          <div class="form-group row">
            <label for="name" class="col-form-label col-lg-2"> Наименование услуги</label>
            <input id="name" class="form-control col-lg-6" type="text" v-model="service.name">
          </div>
          <div class="form-group row">
            <label for="unit" class="col-form-label col-lg-2"> Ед. измерения</label>
            <input id="unit" class="form-control col-lg-6" type="text" v-model="service.unit">
          </div>
          <div class="form-group row">
            <label for="count" class="col-form-label col-lg-2"> Количество</label>
            <input id="count" class="form-control col-lg-6" type="text" v-model="service.count">
          </div>
          <div class="form-group row">
            <label for="price" class="col-form-label col-lg-2"> Цена</label>
            <input id="price" class="form-control col-lg-6" type="text" v-model="service.price">
          </div>
          <div class="form-group row">
            <label for="cost_wo_vat" class="col-form-label col-lg-2"> Стоимость без НДС</label>
            <input id="cost_wo_vat" class="form-control col-lg-6" type="text" readonly v-model="calculate_price_wo_vat">
          </div>
          <div class="form-group row">
            <label for="vat_rate" class="col-form-label col-lg-2"> Ставка НДС</label>
            <input id="vat_rate" class="form-control col-lg-6" type="text" v-model="service.vat">
          </div>
          <div class="form-group row">
            <label for="vat_value" class="col-form-label col-lg-2"> Сумма НДС</label>
            <input id="vat_value" class="form-control col-lg-6" readonly type="text" v-model="calculate_vat_value">
          </div>
          <div class="form-group row">
            <label for="cost_with_vat" class="col-form-label col-lg-2" > Стоимость с НДС</label>
            <input id="cost_with_vat" class="form-control col-lg-6" readonly type="text" v-model="calculate_price_with_vat">
          </div>
          <div class="form-group row">
            <button class="btn btn-ligth col-lg-8 form-control" @click.prevent="new_service" v-if="service.id === null">
              Сохранить
            </button>
            <button class="btn btn-ligth col-lg-8 form-control" @click.prevent="set_service" v-else>
              Сохранить
            </button>
          </div>
          <div class="form-group row">
            <router-link class="btn btn-ligth col-lg-8 form-control" :to="{name:'invoice', params:{id:invoice, add_service:service.id}}" v-if="new_service_for_invoice">
              Вернутся к счету
            </router-link>
          </div>
        </form>
      </div>
  </div>
</template>

<script>
  import constants from '@/common/constants'
    export default {
      name: "Service",
      data(){
        return {
          service: {
            id: null,
            name: '',
            unit: '',
            count: '',
            price: '',
            cost_wo_vat: '',
            vat: 20,
            vat_value: '',
            cost_with_vat: ''
          },
          invoice:''
        }
      },
      computed:{
        calculate_vat_value:function(){
          let result = this.service.cost_wo_vat * this.service.vat / 100;
          result = result.toFixed(2);
          this.service.vat_value =  result;
          return result
        },
        calculate_price_wo_vat:function () {
          let result = this.service.price * this.service.count;
          result = result.toFixed(2);
          this.service.cost_wo_vat = result;
          return this.service.cost_wo_vat
        },
        calculate_price_with_vat:function () {
          let result = Number(this.service.cost_wo_vat) + Number(this.service.vat_value);
          result = result.toFixed(2);
          this.service.cost_with_vat = result;
          return result
        },
        new_service_for_invoice:function () {
          this.invoice = this.$route.params.invoice
          if (this.invoice !== undefined && this.service.id !== null){
            console.log(this.invoice, 'invoice')
            return true
          }
          else {
            return false
          }
        }
      },
      methods:{
        get_service:function (id) {
          this.$http.get(constants.URL_PREFIX+'services/api/service/'+id+'/').then(response => this.service = response.data)
        },
        new_service:function () {
          this.$http.post(constants.URL_PREFIX+'services/api/service/', this.service).then(response => this.service=response.data)
        },
        set_service:function () {
          this.$http.put(constants.URL_PREFIX+'services/api/service/'+this.service.id+'/', this.service).then(response => this.service=response.data)
        }
      },
      created(){
        if (this.$route.params.id !== undefined){
          this.get_service(this.$route.params.id)
        }

      }
    }
</script>

<style scoped>

</style>



