<template>
  <div class="row">
    <div class="col-lg-12">
      <form class="col-lg-10 offset-1" style="width: 100%">
        <!--<span v-if="(($route.path).indexOf('postal') == '1')"-->
              <!--@click="$store.dispatch('PartnerDetailStore/set_postal_address', mapState([pa]))"-->
        <!--&gt;Совпадает с юридическим</span>-->
        <div class="form-group row">
          <label for="full_name" class="col-form-label col-lg-2"> Полное наименование</label>
          <input id="full_name" class="form-control col-lg-6" type="text" v-model="partner.full_name">
        </div>
        <div class="form-group row">
          <label for="short_name" class="col-form-label col-lg-2"> Сокращенное наименование</label>
          <input id="short_name" class="form-control col-lg-6" type="text" v-model="partner.short_name">
        </div>
        <div class="form-group row">
          <label for="address" class="col-form-label col-lg-2"> Адрес</label>
          <input id="address" class="form-control col-lg-6" type="text" v-model="partner.address">
        </div>
        <div class="form-group row">
          <label for="bank_requisites" class="col-form-label col-lg-2"> Банковские реквизиты</label>
          <input id="bank_requisites" class="form-control col-lg-6" type="text" v-model="partner.bank_requisites">
        </div>
        <div class="form-group row">
          <label for="taxpayer_number" class="col-form-label col-lg-2"> УНП</label>
          <input id="taxpayer_number" class="form-control col-lg-6" type="text" v-model="partner.taxpayer_number">
        </div>
        <div class="form-group row">
          <button class="btn btn-ligth form-control" @click.prevent="new_partner" v-if="partner.id === null">
            Сохранить
          </button>
          <button class="btn btn-dark form-control" @click.prevent="set_partner" v-else>
            Сохранить
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
  import constants from '@/common/constants'
    export default {
      name: "Partner",
      data(){
        return{
          partner:{
            id:null,
            full_name:'',
            short_name:'',
            address:'',
            bank_requisites:'',
            is_self_firm:false,
            taxpayer_number:''
          } ,
        }
      },
      methods:{
        get_partner:function (id) {
          this.$http.get(constants.URL_PREFIX+'partners/api/partner/'+id+'/').then(response => this.partner = response.data)
        },
        get_my_firm:function () {
          this.$http.get(constants.URL_PREFIX+'partners/api/partner/?my_firm=True').then(response => this.partner = response.data[0])
        },
        new_partner:function () {
          this.$http.post(constants.URL_PREFIX+'partners/api/partner/', this.partner).then(response => {
            this.partner=response.data;
            alert('Сохранено')
          })
        },
        set_partner:function () {
          this.$http.put(constants.URL_PREFIX+'partners/api/partner/'+this.partner.id+'/', this.partner).
          then(response => {
            this.partner=response.data;
            alert('Сохранено')
          })
        }
      },
      created:function () {
          console.log(this.$route.params['my_firm'])
        if (this.$route.params['my_firm'] === undefined){
          this.get_partner(this.$route.params.id)
        }
        else {
          console.log(this.$route.params['my_firm'])
          this.get_my_firm()
        }
      }
    }
</script>

<style scoped>

</style>



