<template>
  <div class="row">
    <div class="col-lg-12">
      <div class="form-group col-lg-10 offset-1">
        <router-link :to="{name:'partner'}" class="btn btn-dark mt-2 form-control">
            Добавить партнера
        </router-link>
      </div>

      <div class="col-lg-10 offset-1">
        <ul class="list-group" v-for="partner, index in partners">
          <li class="list-group-item list-group-item-action " >
            <div class="row">
              <div class="col-lg-1 btn btn-dark">
                {{index+1}}
              </div>
              <div class="col-lg-2 ">
                <router-link :to="{name:'invoices', params:{partner:partner.id}}" class="btn btn-dark" style="width: 90%">
                  Счета
                </router-link>
              </div>
              <div class="col-lg-9"> <!--@mouseover="show_action_list">-->
                <router-link :to="{name:'partner', params:{id:partner.id}}" class="btn btn-dark" style="width: 100%">
                  {{partner.full_name}}
                </router-link>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import constants from '@/common/constants'
  import Invoice from '@/components/Invoice'
    export default {
        name: "PartnersList",
      data(){
          return{
            partners:'empty',
            hovered:false,
          }
      },
      methods:{
        get_partners:function () {
          this.$http.get(constants.URL_PREFIX+'partners/api/partner/').then(response => {this.partners=response.data})
        },
        show_action_list:function (hide) {
          let self = this
          function reverse_hover() {
            self.hovered = !self.hovered
          }
          if(hide){
            setTimeout(reverse_hover, 0)
          }
          else {
            setTimeout(reverse_hover, 10000)
          }

        }
      },
      created:function () {
        this.get_partners()
      },
      components:{
          Invoice
      }
    }
</script>

<style scoped>

</style>



