import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import PartnersList from '@/components/PartnersList'
import Partner from '@/components/Partner'
import Invoice from '@/components/Invoice'
import Invoices from '@/components/Invoices'
import Service from '@/components/Service'
import Act from '@/components/Act'


Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/partners',
      name: 'partners',
      component: PartnersList
    },
    {
      path: '/partner/:id?/:my_firm?',
      name: 'partner',
      component: Partner
    },
    {
      path: '/invoice/:id?/:add_service?/:partner?',
      name: 'invoice',
      component: Invoice
    },
    {
      path: '/invoices/:partner?/',
      name: 'invoices',
      component: Invoices
    },
    {
      path: '/service/:id?/:invoice?/',
      name: 'service',
      component: Service
    },
    {
      path: '/act/:invoice?/',
      name: 'act',
      component: Act
    },
  ]
})



