<template>
    <div class="row">
      <div class="col-lg-12">
        <form class="">
          <!--<span v-if="(($route.path).indexOf('postal') == '1')"-->
                <!--@click="$store.dispatch('PartnerDetailStore/set_postal_address', mapState([pa]))"-->
          <!--&gt;Совпадает с юридическим</span>-->
          <div class="form-group row">
            <label for="number" class="col-form-label col-lg-2"> Номер документа</label>
            <input id="number" class="form-control col-lg-6" type="text" v-model="invoice.number">
          </div>
          <div class="form-group row">
            <label for="partner" class="col-form-label col-lg-2"> Плательщик</label>
            <input id="partner" class="form-control col-lg-6" type="text" readonly v-model="invoice.partner_obj.full_name">
          </div>
          <div class="form-group row">
            <label for="date" class="col-form-label col-lg-2"> Дата</label>
            <input id="date" class="form-control col-lg-6" type="date" v-model="invoice.date">
          </div>
        </form>
        <div class="table-responsive" v-if="invoice.id !== null">
          <table class="table">
            <tr>
              <th>№ <br> <router-link :to="{name:'service', params:{invoice:invoice.id}}">+</router-link></th>
              <th>Наименование и краткая характеристика услуги</th>
              <th>Ед.изм.</th>
              <th>Кол-во</th>
              <th>Цена,руб.</th>
              <th>Сумма без НДС, руб.</th>
              <th>Ставка НДС, %</th>
              <th>Сумма НДС, руб.</th>
              <th>Сумма с НДС руб.</th>
            </tr>
            <tr v-for="service, index in invoice.services_obj">
              <td>{{index+1}}</td>
              <td><router-link :to="{name:'service', params:{id:service.id}}">{{service.name}}</router-link></td>
              <td>{{service.unit}}</td>
              <td>{{service.count}}</td>
              <td>{{service.price}}</td>
              <td>{{service.cost_wo_vat}}</td>
              <td>{{service.vat}}</td>
              <td>{{service.vat_value}}</td>
              <td>{{service.cost_with_vat}}</td>
            </tr>
            <tr>
              <td colspan="5">Итого</td>
              <td>{{summ_price_wo_vat}}</td>
              <td></td>
              <td>{{summ_vat}}</td>

              <td >{{summ_price_with_vat}}</td>
            </tr>
          </table>
        </div>
        <form class="" v-if="invoice.id !== null">
          <!--<span v-if="(($route.path).indexOf('postal') == '1')"-->
                <!--@click="$store.dispatch('PartnerDetailStore/set_postal_address', mapState([pa]))"-->
          <!--&gt;Совпадает с юридическим</span>-->
          <div class="form-group row">
            <label for="vat_verbose" class="col-form-label col-lg-2"> Сумма НДС прописью</label>
            <input id="vat_verbose" class="form-control col-lg-6" type="text" readonly v-model="num2str_vat">
          </div>
          <div class="form-group row">
            <label for="price_with_vat_verbose" class="col-form-label col-lg-2"> ВСЕГО с НДС прописью: </label>
            <input id="price_with_vat_verbose" class="form-control col-lg-6" type="text" readonly v-model="num2str_price_with_vat">
          </div>
        </form>
        <form>
          <div class="form-group row">
            <button class="btn btn-dark col-lg-8 form-control d-print-none" @click.prevent="new_invoice" v-if="invoice.id === null">
              Сохранить
            </button>
            <button class="btn btn-dark col-lg-8 form-control d-print-none" @click.prevent="set_invoice" v-else>
              Сохранить
            </button>
          </div>
          <div class="form-group row d-print-none">
            <router-link class="btn btn-dark col-lg-8 form-control" :to="{name:'act', params:{invoice:invoice.id}}">
              Акт
            </router-link>
          </div>
        </form>
        <div class="row ">
          <div class="col-lg-10 offset-1 ">
            <div class="table-responsive">
              <table style="width: 100%;">
                <tr>
                  <td style="width: 50%;">
                    <ul class="list-group" >
                      <li class="list-group-item" >
                        <h3>{{my_firm.full_name}}</h3>
                      </li>
                      <li class="list-group-item">
                        {{my_firm.address}}
                      </li>
                      <li class="list-group-item">
                        {{my_firm.taxpayer_number}}
                      </li>
                      <li class="list-group-item">
                        {{my_firm.bank_requisites}}
                      </li>
                    </ul>
                  </td>

                  <td style="width: 50%;">
                    <ul class="list-group" >
                      <li class="list-group-item" >
                        <h3>{{invoice.partner_obj.full_name}}</h3>
                      </li>
                      <li class="list-group-item">
                        {{invoice.partner_obj.address}}
                      </li>
                      <li class="list-group-item">
                        {{invoice.partner_obj.taxpayer_number}}
                      </li>
                      <li class="list-group-item">
                        {{invoice.partner_obj.bank_requisites}}
                      </li>
                    </ul>
                  </td>
                </tr>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  import constants from '@/common/constants/'
  import num_to_phrase from '@/common/num_to_phrase/'
    export default {
      name: "Invoice",
      data(){
        return{
          invoice:{
            id:null,
            number:'',
            partner:'',
            partner_obj:{},
            services:[],
            services_obj:[],
            price_with_vat_value:null,
            price_with_vat_verbose:'',
            price_wo_vat_value:null,
            price_wo_vat_verbose:'',
            vat_value:null,
            vat_verbose:'',
            date:'',
          },
          my_firm:{},
        }
      },
      computed:{
        summ_price_with_vat:function () {
          let summ = 0;
          this.invoice.services_obj.forEach(function (value, index) {
            summ +=  (parseFloat(value.cost_with_vat))
          });
          summ = summ.toFixed(2);
          this.invoice.price_with_vat_value = summ;
          return summ > 0 ? ( summ) :( null)
        },
        summ_price_wo_vat:function () {
          let summ = 0;
          this.invoice.services_obj.forEach(function (value, index) {
            summ +=  (parseFloat(value.cost_wo_vat))
          });
          summ = summ.toFixed(2);
          this.invoice.price_wo_vat_value = summ;
          return summ > 0 ? ( summ) :( null)
        },
        summ_vat:function () {
          let summ = 0;
          this.invoice.services_obj.forEach(function (value, index) {
            summ +=  (parseFloat(value.vat_value))
          });
          summ = summ.toFixed(2);
          this.invoice.vat_value = summ;
          return summ > 0 ? ( summ) :( null)
        },
        num2str_vat:function () {
          let result = num_to_phrase.money2string(parseFloat(this.invoice.vat_value));
          this.invoice.vat_verbose = result
          return result
        },
        num2str_price_with_vat:function () {
          let result = num_to_phrase.money2string(parseFloat(this.invoice.price_with_vat_value));
          this.invoice.price_with_vat_verbose = result
          return result
        },
      },
      methods:{
        get_invoice:function (invoice_id) {
          this.$http.get(constants.URL_PREFIX+'documents/api/documents/'+invoice_id+'/').then(response => {
            console.log(response.data)
            this.invoice=response.data})
        },
        get_partner:function (partner_id) {
          this.$http.get(constants.URL_PREFIX+'partners/api/partner/'+partner_id+'/').then(response => {
            this.invoice.partner_obj = response.data;
            this.invoice.partner = response.data.id
          })
        },
        get_my_firm:function () {
          this.$http.get(constants.URL_PREFIX+'partners/api/partner/?my_firm=True').then(response => this.my_firm = response.data[0])
        },
        get_service:function (service_id) {
          this.$http.get(constants.URL_PREFIX+'services/api/service/'+service_id+'/').then(response => {
            console.log(response.data)
            this.invoice.services.push(response.data.id);
            this.invoice.services_obj.push(response.data);
          })
        },
        new_invoice:function () {
          this.$http.post(constants.URL_PREFIX+'documents/api/documents/', this.invoice).then(response => this.invoice=response.data)
        },
        set_invoice:function () {
          this.$http.put(constants.URL_PREFIX+'documents/api/documents/'+this.invoice.id+'/', this.invoice).
            then(response => {
              this.invoice=response.data;
              alert('Сохранено')
            })
        }

      },
      created(){
        let invoice = this.$route.params.id !== undefined ? this.$route.params.id : null
        console.log(this.$route.params.id)
        if (this.$route.params.add_service !== undefined){
          this.get_invoice(invoice);
          let self = this;
          function get_service(){
            return self.get_service(self.$route.params.add_service)
          }
          setTimeout(get_service, 1000)
        }
        else {
          if (invoice !== null) {
            this.get_invoice(invoice)
          }
          else{
            this.get_partner(this.$route.params.partner)
          }
          this.get_service(this.$route.params.add_service)
        }
        this.get_my_firm()
      }
    }
</script>

<style scoped>

</style>



